* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: rgba(218, 138, 8, .475);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.wave, .wave:before, .wave:after {
  content: "";
  width: 250vw;
  height: 250vw;
  transform-origin: 50%;
  mix-blend-mode: multiply;
  z-index: 0;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 38% 42%;
  margin-left: -125vw;
  animation: spin 16s linear infinite;
  position: absolute;
  top: 80%;
  left: 50%;
  box-shadow: inset 0 0 10vw rgba(255, 0, 255, .8);
}

.wave:before {
  width: 105%;
  height: 95%;
  transform-origin: 49% 51%;
  z-index: 0;
  border-radius: 40% 38%;
  margin-top: -125vw;
  animation: spin 13s linear infinite;
  box-shadow: inset 0 0 10vw rgba(255, 255, 0, .8);
}

.wave:after {
  width: 102%;
  height: 98%;
  transform-origin: 51% 49%;
  z-index: 0;
  z-index: 0;
  border-radius: 48% 42%;
  margin-top: -170vw;
  animation: spin 10s linear infinite;
  overflow: visible;
  box-shadow: inset 0 0 10vw rgba(0, 255, 255, .8);
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.custom-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: none;
  border-radius: 5px;
  outline: none;
  margin-top: 10px;
  margin-left: 10px;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.btn-6 {
  text-align: center;
  background: radial-gradient(circle, #f796c0 0%, #76aef1 100%);
  border: none;
  padding: 0;
  line-height: 42px;
}

.btn-6 span {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.btn-6:before, .btn-6:after {
  content: "";
  height: 0%;
  width: 1px;
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.btn-6:before {
  transition: all .5s;
  top: 0;
  right: 0;
}

.btn-6:after {
  transition: all .5s;
  bottom: 0;
  left: 0;
}

.btn-6:hover {
  color: #9517b4;
  box-shadow: none;
  background: rgba(0, 255, 255, .286);
}

.btn-6:hover:before, .btn-6:hover:after {
  height: 100%;
  transition: all .5s;
}

.btn-6 span:before, .btn-6 span:after {
  content: "";
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.btn-6 span:before {
  width: 0%;
  height: .5px;
  transition: all .5s;
  top: 0;
  left: 0;
}

.btn-6 span:after {
  width: 0%;
  height: .5px;
  transition: all .5s;
  bottom: 0;
  right: 0;
}

.btn-6 span:hover:before, .btn-6 span:hover:after {
  width: 100%;
}

.goback {
  text-align: center;
}

.slight {
  width: 150px;
  height: 80px;
}

.player {
  z-index: 3;
  position: relative;
}

